import React, { useEffect } from "react";
import { useHistory } from "react-router";
import { useSelector, useDispatch } from "react-redux";
import {
  obtainOrigin,
  selectOrigin,
  selectOriginLoaded,
  obtainUnit,
  selectUnit,
  selectUnitLoaded,
  obtainTopic,
  selectTopic,
  selectTopicLoaded,
  selectSubTopic,
  selectSubTopicLoaded,
  obtainSubTopic,
  selectedOrigin,
  selectedUnit,
  selectedTopic,
  selectedSubTopic,
  selectedDateRangeSince,
  selectedDateRangeUntil,
  selectedNumberRangeFrom,
  selectedNumberRangeTo,
  selectFormData,
  obtainSignatorys,
  selectSignatorys,
  selectSignatorysLoaded,
  selectedSignatory,
  obtainPersonnels,
  selectPersonnelsLoaded,
  selectPersonnels,
  selectedPersonnel,
} from "./SearchFormSlice";
import { reSearch } from "../ResultsTable/ResultsSlice";

function SearchForm() {
  const origin = useSelector(selectOrigin);
  const originLoaded = useSelector(selectOriginLoaded);
  const unit = useSelector(selectUnit);
  const unitLoaded = useSelector(selectUnitLoaded);
  const topics = useSelector(selectTopic);
  const topicLoaded = useSelector(selectTopicLoaded);
  const subTopics = useSelector(selectSubTopic);
  const subTopicLoaded = useSelector(selectSubTopicLoaded);
  const signatorys = useSelector(selectSignatorys);
  const signatorysLoaded = useSelector(selectSignatorysLoaded);
  const personnels = useSelector(selectPersonnels);
  const personnelsLoaded = useSelector(selectPersonnelsLoaded);
  const formData = useSelector(selectFormData);
  let history = useHistory();
  const dispatch = useDispatch();

  useEffect(() => {
    if (!originLoaded) {
      dispatch(obtainOrigin());
    }

    if (!unitLoaded) {
      dispatch(obtainUnit());
    }

    if (!signatorysLoaded) {
      dispatch(obtainSignatorys());
    }

    if (!personnelsLoaded) {
      dispatch(obtainPersonnels());
    }
  });

  function setSelectedOrigin(value) {
    dispatch(obtainTopic(value));
    dispatch(selectedOrigin(value));
  }

  function setSelectedTopic(value) {
    dispatch(obtainSubTopic(value));
    dispatch(selectedTopic(value));
  }

  function search(event) {
    history.push("/inta_front/resultados");
    dispatch(reSearch());
  }

  return (
    <div className="panelcontainer">
    <div style={{marginLeft:20, marginRight:20}} >
      <section>
        <div className="row">
          <div className="col-md-12 ">
            <h1 style={{ textAlign: "center" }}>
              Consulta General de la Base de Datos
            </h1>

            <hr />

            <form>
              <fieldset>
                <div className="row">
                  <div className="col-md-4">
                    <div className="form-group  item-form">
                      <label>Palabra clave</label>
                      <input
                        type="text"
                        class="form-control"
                        onChange={(e) => null}
                      />
                    </div>
                  </div>
                  <div className="col-md-4">
                    <div className="form-group item-form">
                      <label>Fuente/Origen</label>
                      <select
                        className="form-control"
                        onChange={(e) => setSelectedOrigin(e.target.value)}
                      >
                        {origin && origin.length > 0 && originLoaded ? (
                          origin.map((option) => (
                            <option key={option.id} value={option.id}>
                              {option.text}
                            </option>
                          ))
                        ) : (
                          <option value="Todos" selected="">
                            Obteniendo datos...
                          </option>
                        )}
                      </select>
                    </div>
                  </div>

                  <div className="col-md-4">
                    <div className="form-group item-form">
                      <label>Unidades</label>
                      <select
                        className="form-control"
                        onChange={(e) => dispatch(selectedUnit(e.target.value))}
                      >
                        {unit && unit.length > 0 && unitLoaded ? (
                          unit.map((option) => (
                            <option key={option.id} value={option.id}>
                              {option.text}
                            </option>
                          ))
                        ) : (
                          <option value="Todos" selected="">
                            Obteniendo datos...
                          </option>
                        )}
                      </select>
                    </div>
                  </div>
                </div>
                <div className="row">
                  <div className="col-md-3">
                    <div className="form-group item-form">
                      <label>Tema</label>
                      <select
                        className="form-control"
                        onChange={(e) => setSelectedTopic(e.target.value)}
                      >
                        {topics && topics.length > 0 && topicLoaded ? (
                          topics.map((option) => (
                            <option key={option.id} value={option.id}>
                              {option.text}
                            </option>
                          ))
                        ) : (
                          <option value="Todos" selected="">
                            TODOS
                          </option>
                        )}
                      </select>
                    </div>
                  </div>

                  <div className="col-md-3">
                    <div className="form-group item-form">
                      <label>Subtema</label>
                      <select
                        className="form-control"
                        onChange={(e) =>
                          dispatch(selectedSubTopic(e.target.value))
                        }
                      >
                        {subTopics && subTopics.length > 0 && subTopicLoaded ? (
                          subTopics.map((option) => (
                            <option key={option.id} value={option.id}>
                              {option.text}
                            </option>
                          ))
                        ) : (
                          <option value="Todos" selected="">
                            TODOS
                          </option>
                        )}
                      </select>
                    </div>
                  </div>

                  <div className="col-md-3">
                    <div className="form-group item-form">
                      <label>Firmante</label>
                      <select
                        className="form-control"
                        onChange={(e) =>
                          dispatch(selectedSignatory(e.target.value))
                        }
                      >
                        {signatorys &&
                        signatorys.length > 0 &&
                        signatorysLoaded ? (
                          signatorys.map((option) => (
                            <option key={option.id} value={option.id}>
                              {option.text}
                            </option>
                          ))
                        ) : (
                          <option value="Todos" selected="">
                            TODOS
                          </option>
                        )}
                      </select>
                    </div>
                  </div>
                  <div className="col-md-3">
                    <div className="form-group item-form">
                      <label>Personal</label>
                      <select
                        className="form-control"
                        onChange={(e) =>
                          dispatch(selectedPersonnel(e.target.value))
                        }
                      >
                        {personnels &&
                        personnels.length > 0 &&
                        personnelsLoaded ? (
                          personnels.map((option) => (
                            <option key={option.id} value={option.id}>
                              {option.text}
                            </option>
                          ))
                        ) : (
                          <option value="Todos" selected="">
                            TODOS
                          </option>
                        )}
                      </select>
                    </div>
                  </div>
                </div>

                <div className="row">
                  <div className="col-md-3">
                    <div className="form-group  item-form">
                      <label>Rango Fechas</label>
                      <p className="help-block">Desde</p>
                      <input
                        type="date"
                        className="form-control"
                        required=""
                        aria-required="true"
                        onChange={(e) =>
                          dispatch(selectedDateRangeSince(e.target.value))
                        }
                      />
                    </div>
                  </div>
                  <div className="col-md-3">
                    <div className="form-group  item-form">
                      <span style={{ display: "block", height: 16 }}></span>
                      <p className="help-block">Hasta</p>
                      <input
                        type="date"
                        className="form-control"
                        required=""
                        aria-required="true"
                        onChange={(e) =>
                          dispatch(selectedDateRangeUntil(e.target.value))
                        }
                      />
                    </div>
                  </div>
                  <div className="col-md-3">
                    <div className="form-group  item-form">
                      <label>Rango de Numeraci√≥n</label>
                      <p className="help-block">Desde</p>
                      <input
                        type="number"
                        min="1"
                        class="form-control"
                        required=""
                        aria-required="true"
                        onChange={(e) =>
                          dispatch(selectedNumberRangeFrom(e.target.value))
                        }
                      />
                    </div>
                  </div>
                  <div className="col-md-3">
                    <div className="form-group  item-form">
                      <span style={{ display: "block", height: 16 }}></span>
                      <p className="help-block">Hasta</p>
                      <input
                        type="number"
                        min="1"
                        class="form-control"
                        required=""
                        aria-required="true"
                        onChange={(e) =>
                          dispatch(selectedNumberRangeTo(e.target.value))
                        }
                      />
                    </div>
                  </div>
                </div>
                <div style={{ display: "flex", justifyContent: "center", marginBottom:-15 }}>
                  <div>
                    <div className="col-md-8">
                      <button
                        type="button"
                        className="btn btn-success"
                        onClick={(e) => search(e)}
                      >
                        Consultar
                      </button>
                    </div>
                  </div>
                </div>
              </fieldset>
            </form>
          </div>
        </div>
      </section>
    </div>
    </div>
  );
}

export default SearchForm;
